import request from '@/utils/request'

export function getBanner(channel) {//
  const data={
    channel
  }
  return request({
    url:'/index/banner',
    method:'post',
    data
  })
}

export function getTopic() {//
  return request({
    url:'/index/topic',
    method:'post',
  })
}
export function getSort() {//
  return request({
    url:'/index/class',
    method:'post',
  })
}
export function getCateRecipe() {//
  return request({
    url:'/index/categoryrecipe',
    method:'post',
  })
}
export function getRecipe(page) {//
  const data={
    page
  }
  return request({
    url:'/index/featuredrecipe',
    method:'post',
    data
  })
}



